@import url(https://fonts.googleapis.com/css2?family=Ramabhadra&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.searchOptions__container{display:flex;flex-direction:row;justify-content:center;padding:5px 0}

.search__container{display:flex;flex-direction:column;justify-content:center;padding:10px;text-align:center}.search__container .search__input_row{width:100%;display:flex;flex-direction:row;justify-content:center;margin:10px auto}.search__container .search__options_row{width:100%;display:flex;flex-direction:row;justify-content:center;margin:10px auto}.search__container .search__input{width:75%;line-height:normal}.search__container .search__button{padding:15px;margin:0 10px}.search__container .trending__button{padding:15px 25px;margin:0 10px;max-height:55px}

.grid{display:flex;flex-wrap:wrap}.my-masonry-grid{display:flex;margin-left:-30px;width:auto}.my-masonry-grid_column{padding-left:0;background-clip:padding-box}.my-masonry-grid_column>div{background:grey}.noGifs{text-align:center}a{text-decoration:none;color:#fff}.infoLabel{font-size:10px}@media (min-width: 480px){.whatsappShare{display:none}}


